import React from 'react'
import {
  Segment,
  Container,
  Grid,
  List,
  Header,
  Image
} from 'semantic-ui-react'
import code from '../../src/assert/img/code.jpeg'

const BaseFooter = () => (
  <div style={{overflow: 'hidden'}}>
    <Segment inverted vertical style={{ padding: '5em 0em' }}>
      <Container>
        <Grid divided inverted stackable>
          <Grid.Row>
            <Grid.Column width={5}>
              <Header inverted as='h4' content='Tic' />
              <List link inverted>
                <List.Item></List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={5}>
              <Header inverted as='h4' content='' />
              <List link inverted>
                <List.Item>S@banketime.com</List.Item>
                <List.Item>2</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={5}>
              <Header as='h4' inverted></Header>
              <Image size='tiny' src={code} />
              <p style={{color: 'rgba(255,255,255,.5)', paddingTop: '.7rem'}}></p>
            </Grid.Column>
          </Grid.Row>
        </Grid>
      </Container>
    </Segment>
    <div style={{
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      height: '4rem',
      background: '#1b1c1d',
      borderTop: '1px solid #333'
    }}>
      <p style={{ width: '80%', textAlign: 'center', color: 'rgba(255,255,255,.5)'}}>Copyright&nbsp;@&nbsp;2018&nbsp;&nbsp;ICP16016214-1</p>
    </div>
  </div>

)

export default BaseFooter



